<template>
  <div>
    <!--<div style="width: 100%;height:80px;background-color: #409eff;"><img style="margin-left: 10px;margin-top: 25px;" src="../../../static/img/mxkj.jpg"/></div>-->
    <div style="margin-top: 20px;">
      <el-select style="width: 120px" v-model="queryType" slot="prepend" class="filter-item">
        <el-option value="listQuery.rfid" label="rfid设备号"></el-option>
        <el-option value="listQueryImei.imei" label="imei号"></el-option>
      </el-select>
      <el-input style="width: 200px;" class="filter-item" placeholder="设备号" v-model="queryValue"></el-input>
      <el-date-picker style="width: 200px;"
                      class="filter-item"
                      v-model="listQuery.start"
                      type="datetime"
                      :picker-options="limitTime.start_time"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      placeholder="开始时间">
      </el-date-picker>
      <!--value-format="yyyy-MM-dd HH:mm:ss" 将2018-12-17T16:35:17Z转为2018-12-17+16:35:17 传递参数的时候“+”被识别为空格-->
      <el-date-picker style="width: 200px;"
                      class="filter-item"
                      v-model="listQuery.end"
                      type="datetime"
                      :picker-options="limitTime.end_time"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      placeholder="结束时间">
      </el-date-picker>
      <el-button type="primary" @click="handleQuery">搜索</el-button>
    </div>
    <!--输入数据进行搜索-->
    <div style="margin-top: 20px;">
      <el-table ref="multipleTable"
                :data="list"
                tooltip-effect="dark"
                border fit highlight-current-row style="width: 100%;"
                height="600"
                row-key="id">
        <el-table-column width="65" align="center" label="序号" prop="index"></el-table-column>
        <el-table-column width="center" align="center" label="imei号" prop="imei"></el-table-column>
        <el-table-column width="center" align="center" label="编号" prop="id"></el-table-column>
        <el-table-column width="center" align="center" label="设备号" prop="device_rfid"></el-table-column>
        <el-table-column align="center" label="内容" prop="content"></el-table-column>
        <el-table-column align="center" label="时间" prop="create_time" :formatter="dateFormat"></el-table-column>
      </el-table>
    </div>
    <!--显示从接口获取的数据-->
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="listQuery.page"
        :page-sizes="pageSizes"
        :page-size="listQuery.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
      <!--分页展示-->
    </div>
  </div>
</template>

<script>
import trackApi from '@/api/device/trackexport.js'
export default {
  name: 'message1',
  data () {
    return {
      listQuery: {
        offset: 0,
        limit: 10,
        rfid: null,
        start: '',
        end: ''
      }, // 传递给接口的参数，可同时查询所有设备号和时间
      listQueryAll: {
        offset: 0,
        limit: 10,
        imei: null,
        start: '',
        end: ''
      }, // 传递给接口的参数，可同时查询所有imei和时间
      listQueryId: {
        offset: '',
        limit: '',
        rfid: null
      }, // 传递给接口的参数，可查询设备号
      listQueryImei: {
        offset: '',
        limit: '',
        imei: null
      }, // 传递给接口的参数，可查询设备号
      listQueryTime: {
        offset: '',
        limit: '',
        start: '',
        end: ''
      }, // 传递给接口的参数，可查询时间
      listQueryLimit: {
        offset: '',
        limit: ''
      }, // 传递给接口的参数，限制页面显示
      limitTime: {
        start_time: this.startTime(), // 调用函数控制开始时间范围
        end_time: this.endTime() // 调用函数控制结束时间范围
      }, // 限制时间范围的参数
      list: null, // 从接口获取数据显示的列表
      total: null, // 从接口获取数据的总数
      queryType: 'listQueryImei.imei', // 默认查询条件
      queryValue: '', // 输入框 输入的值
      pageSizes: [5, 10, 20, 30, 50], // 设置每页显示的数据条数，可修改
      dateFormat (row, column) {
        const dateMat = row[column.property].replace('T', ' ').replace('Z', '')
        return dateMat
      } // 转换时间格式将2018-12-17T16:35:17Z转为2018-12-17 16:35:17显示
    }
  },
  created () {
    this.getList1()
  },
  methods: {
    handleSizeChange (val) {
      this.listQuery.limit = val
      this.handleQuery()
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      this.listQuery.offset = (val - 1) * this.listQuery.limit
      this.handleQuery()
      console.log(`当前页: ${val}`)
    },
    getList () {
      this.$http.get('http://192.168.1.231:8088/v1/api/rf_demo/deviceInfo').then(res => {
        console.log(res)
      })
    }, // 测试axios连接接口
    getList1 () {
      var _this = this
      this.listQueryLimit.limit = this.listQuery.limit
      this.listQueryLimit.offset = this.listQuery.offset
      trackApi.queryDeviceRecords(this.listQueryLimit).then(res => {
        const data = res.data.result.info
        data.forEach(function (key, index) {
          key.index = index + 1 + _this.listQuery.offset
        })
        this.list = data
        this.total = res.data.result.count
      })
    }, // axios获取设备接口数据 显示所有数据
    getList2 () {
      var _this = this
      this.listQueryAll.limit = this.listQuery.limit
      this.listQueryAll.offset = this.listQuery.offset
      this.listQueryAll.imei = this.listQueryImei.imei
      this.listQueryAll.start = this.listQuery.start
      this.listQueryAll.end = this.listQuery.end
      trackApi.queryDeviceRecords(this.listQueryAll).then(res => {
        const data = res.data.result.info
        data.forEach(function (key, index) {
          key.index = index + 1 + _this.listQuery.offset
        })
        this.list = data
        this.total = res.data.result.count
      })
    }, // axios获取imei号 搜索imei 时间
    getList3 () {
      var _this = this
      // _this.offset = this.limit * (this.page - 1)
      trackApi.queryDeviceRecords(this.listQuery).then(res => {
        console.log(res.data.result)
        const data = res.data.result.info
        data.forEach(function (key, index) {
          key.index = index + 1 + _this.listQuery.offset
        })
        this.list = data
        this.total = res.data.result.count
      })
    }, // axios获取接口数据 同时搜索时间和设备号
    getList4 () {
      var _this = this
      this.listQueryTime.limit = this.listQuery.limit
      this.listQueryTime.offset = this.listQuery.offset
      this.listQueryTime.start = this.listQuery.start
      this.listQueryTime.end = this.listQuery.end
      trackApi.queryDeviceRecords(this.listQueryTime).then(res => {
        const data = res.data.result.info
        data.forEach(function (key, index) {
          key.index = index + 1 + _this.listQuery.offset
        })
        this.list = data
        this.total = res.data.result.count
      })
    }, // axios获取时间接口数据 搜索时间
    getList5 () {
      var _this = this
      this.listQueryImei.limit = this.listQuery.limit
      this.listQueryImei.offset = this.listQuery.offset
      // this.listQueryImei.imei = this.listQuery.offset
      trackApi.queryDeviceRecords(this.listQueryImei).then(res => {
        const data = res.data.result.info
        data.forEach(function (key, index) {
          key.index = index + 1 + _this.listQuery.offset
        })
        this.list = data
        this.total = res.data.result.count
      })
    }, // axios获取imei号 搜索imei
    getList6 () {
      var _this = this
      this.listQueryId.limit = this.listQuery.limit
      this.listQueryId.offset = this.listQuery.offset
      this.listQueryId.rfid = this.listQuery.rfid
      trackApi.queryDeviceRecords(this.listQueryId).then(res => {
        console.log(this.listQueryId)
        const data = res.data.result.info
        data.forEach(function (key, index) {
          key.index = index + 1 + _this.listQuery.offset
        })
        this.list = data
        this.total = res.data.result.count
      })
    }, // axios获取设备号接口数据 搜索设备号
    startTime () {
      let _this = this
      return {
        disabledDate (time) {
          if (_this.listQuery.end) {
            return time.getTime() >= new Date(new Date(_this.listQuery.end).getTime() + 1000 * 60 * 60 * 24) || time.getTime() < new Date(new Date() - 1000 * 60 * 60 * 24 * 30)
          } else {
            return time.getTime() > new Date() || time.getTime() < new Date(new Date() - 1000 * 60 * 60 * 24 * 30)
          }
        }
      }
    }, // 限制开始时间的选择
    endTime () {
      let _this = this
      return {
        disabledDate (time) {
          if (_this.listQuery.start) {
            return time.getTime() > new Date() || time.getTime() <= new Date(new Date(_this.listQuery.start).getTime() - 1000 * 60 * 60 * 24)
          } else {
            return time.getTime() > new Date() || time.getTime() < new Date(new Date() - 1000 * 60 * 60 * 24 * 30)
          }
        }
      }
    }, // 限制结束时间的选择
    handleQuery () {
      if (this.queryValue) {
        if (this.queryType === 'listQueryImei.imei') {
          this.listQueryImei.imei = this.queryValue
        } else if (this.queryType === 'listQuery.rfid') {
          this.listQuery.rfid = this.queryValue
        }
      } else {
        this.listQueryImei.imei = null
        this.listQuery.rfid = null
      }
      if (this.listQuery.start && this.listQuery.end) {
        if (this.listQuery.start > this.listQuery.end) {
          this.$message({ message: '开始时间要小于结束时间！', type: 'warning', duration: 1500 })
        } else {
          if (this.listQueryImei.imei) {
            this.getList2()
          } else if (this.listQuery.rfid) {
            this.getList3()
          } else {
            this.getList4()
          }
        }
      } else if (this.listQueryImei.imei != null) {
        this.getList5()
      } else if (this.listQuery.rfid != null) {
        this.getList6()
      } else {
        this.getList1()
      }
    } // 根据输入内容选择调用接口
  }
}
</script>
<style scoped>

</style>



