import fetch from '@/utils/fetch'

const api = {

  queryDeviceRecords (query) {
    return fetch({
      url: '/api/rf_demo/deviceInfo',
      method: 'get',
      params: query
    })
  },
  queryDeviceID (query) {
    return fetch({
      url: '/api/rf_demo/deviceInfoByID',
      method: 'get',
      params: query
    })
  },
  queryDeviceTime (query) {
    return fetch({
      url: '/api/rf_demo/deviceInfoByTime',
      method: 'get',
      params: query
    })
  }
}

export default api



